  float: left;
  margin-left: 13%;
  height: 23rem;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  background-color: #fef9f8;
}

.projectSection .projectCard .projectTitle {
  text-align: center;
  margin: 0;
  color: #2B4570;
}

.projectSection .projectCard .projectImg {
  width: 100%;
  max-height: 50%;
}

.projectSection .projectCard .projectDesc {
  margin-top: -4px;
  margin-bottom: -2px;
  min-height: 21%;
  font-family: "Noto Sansa KR", sans-serif;
  font-size: 18px;
  font-weight: 700;
  color: #2B4570;
}

.projectSection .projectLinks {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  float: left;
  border: solid 2px;
  background: transparent;
  width: 6rem;
  height: 2rem;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: 0.3s;
  margin: 1rem;
  font-size: 15px;
  text-decoration: none;
}

.projectSection .rep {
  border-color: black;
